* {
    font-family: "Segoe UI", sans-serif;
    box-sizing: border-box;
    font-size: 15px;
    overflow: hidden;
}

body {
    position: absolute;
    background-color: #dff5ff;
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

.sidebar {
    background-color: #dff5ff;
    display: flex;
    width: 42%;
    height: 100%;
    position: absolute;
    z-index: -1;
    left: 0;
    justify-content: center;
}

.form {
    position: fixed;
    background-color: white;
    width: 58%;
    height: 100%;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.new-item-form {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    width: 70%;
    margin-top: 50px;
    z-index: 0;
    right: 0;
    padding: 5px;
}

.background {
    position: absolute;
    width: 100%;
    z-index: -1;
    left: -1;
    bottom: 0;
    margin-bottom: -50px;
}

.logo {
    top: 0;
    display: flex;
    position: absolute;
    width: 200px;
    height: 50%;
}

.logoImage {
    margin-top: 50px;
    width: 200px;
    height: 80px;
}

.btn {
    background-color: blue;
    color: white;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
    width: 200px;
    height: 50px;
    align-self: center;
}

.btn:hover,
.btn:focus-visible {
    background: hsl(200, 100%, 50%, 0.2);
}

.google:hover,
.google:focus-visible {
    background: hsl(200, 100%, 50%, 0.2);
}

.apple:hover,
.apple:focus-visible {
    background: hsla(0, 0%, 42%, 0.2);
}

.forgotPass {
    text-decoration: none;
    color: blue;
    position: absolute;
    right: 0;
    margin-top: -70px;
    margin-right: 10px;
}

.header {
    font-size: 1.5rem;
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    width: 100%;
    margin: 0;
    font-weight: bold;
}


.or-separator {
    display: flex;
    flex-direction: row;
    height: 100px;
    margin: 0 10px;
}

.separator {
    border-top: solid rgb(163, 160, 160);
    color: lightgray;
    width: 100%;
    margin: 50px 0;
}

.or-text {
    display: flex;
    align-items: center;
    color: lightgray;
    padding: 10px;
    height: 50px;
    margin-top: 25px;
}

.create-account {
    position: absolute;
    text-decoration: underline;
    color: black;
    right: 0;
    z-index: 1;
    margin-top: 25px;
    margin-right: 50px;
}

.otherSignIn {
    font-weight: bold;
    display: flex;
    justify-content: space-around;
}

.google {
    border: none;
    width: 250px;
    height: 40px;
    background-color: lightskyblue;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}

.apple {
    border: none;
    width: 250px;
    height: 40px;
    background-color: black;
    color: white;
    border-radius: 10px;
    cursor: pointer;
}

.sub-header {
    margin-top: -10px;
}

.link {
    padding: 10px;
    text-decoration: none;
    color: white;
}

.input {
    padding: 10px;

}

.checkbox {
    display: flex;
    width: 200px;
    margin-left: 10px;

}

.form .new-item-form .input input {
    background-color: rgb(196, 196, 196);
}